<template>
  <div>
    <van-nav-bar title="修改登录密码" left-arrow left-text="返回" @click-left="$router.back()"></van-nav-bar>
    <van-cell-group>
      <van-field v-model="password" clearable label="旧密码" placeholder="请输入旧密码"  />
      <van-field v-model="newPassword" clearable label="新密码" placeholder="请输入新密码"  />
      <van-button type="primary" size="large" @click="confirm">确认修改</van-button>
    </van-cell-group>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        password:'',
        newPassword:''
      }
    },
    methods: {
      confirm(){
        this.$axios.post('/user/updatePassword', {
          password:this.password,
          newPassword:this.newPassword
        }).then(res => {
          if(res.errcode === 0){
            this.$Toast('登录密码修改成功')
            this.$router.back()
          }else{
            this.$Toast(res.errmsg)
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


